import React from 'react';

import './style.css' 
const ImageLinkForm= ({onInputChange,onButtonSubmit}) => {
return(
<div className='tc'>
<p className='f3 white '>
Hey Hi this is Fahad.I'll detect faces in your picture!!
</p>
<div className=' tc ' style={{display:'flex' , justifyContent:'center'}}>
<div className='tc pattern pa4 br3 shadow-5' style={{display:'flex'}}>
<input className='f4 pa2 w-70 br3 shadow-5' type='text' placeholder='Enter image url' onChange={onInputChange}/>

<button className='w-30 pa2 grow f3 link ph3 pv2 dib while bg-yellow br3' onClick={onButtonSubmit}>Detect</button>
</div>
</div>
</div>
);

}

export default ImageLinkForm;


