import React from 'react';


const Navigation= ({onRouteCh,isSignedIn}) => {
if(isSignedIn)
{ 
return(

<nav style={{display:'flex',justifyContent:'flex-end'}}>
<p onClick={()=>onRouteCh('signout')} className='f3 dim pa3 link underline black pointer bg-light-blue'>Sign Out</p>
</nav>
);
}
else
{
return(
	<nav style={{display:'flex',justifyContent:'flex-end'}}>
<p onClick={()=>onRouteCh('signin')} className='f3 dim ba b--orange pa3 link br-pill underline black pointer bg-light-blue'>Sign In</p>


<p onClick={()=>onRouteCh('register')} className='f3 dim ba b--orange pa3 link underline br-pill black pointer bg-light-blue'>Register</p>
</nav>

	);


}

}

export default Navigation;


