
import React from 'react';
import recog from './recog.png'
import Tilt from 'react-tilt';
const Logo= () => {
return(

<div className='ma4 mt0' >
<Tilt className="Tilt br3 shadow-2 bg-black" options={{ max : 70 }} style={{ height: 150, width: 150 }} >
 <div className="Tilt-inner pa1"> <img alt='logo' src={recog}/> </div>
</Tilt>
</div>
);

}

export default Logo;


